import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import {
  TextInput,
  NumberInput,
  ComposedModal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Button,
  Icon
} from  'carbon-components-react';
import { 
  iconDelete, iconDeleteSolid, iconDeleteOutline, 
} from 'carbon-icons';

// This defines a modal controlled by a launcher button. We have one per DataTable row.
const ModalStateManager = ({
  renderLauncher: LauncherContent,
  children: ModalContent,
}) => {
  const [open, setOpen] = useState(false);
  return (
    <>
      {!ModalContent || typeof document === 'undefined'
        ? null
        : ReactDOM.createPortal(
            <ModalContent  
              open={open} 
              setOpen={setOpen} 
            />,
            document.body
          )
      }
      {LauncherContent && 
      <LauncherContent 
        open={open} 
        setOpen={setOpen} 
      />
      }
    </>
  );
};

// Modal properties
const deleteProps = {
  composedModal: ({ titleOnly } = {}) => ({
    open: true,
    danger: true,
    selectorPrimaryFocus: '[data-modal-primary-focus]',
  }),
  modalHeader: ({ titleOnly, name } = {}) => ({
    label: 'Credential',
    title: 'Delete Credential?',
    iconDescription: 'Close',
  }),
  modalBody: () => ({
    hasScrollingContent: false,
    'aria-label': 'Delete Credential?',
  }),
  modalFooter: () => ({
    primaryButtonText: 'Delete',
    primaryButtonDisabled: false,
    secondaryButtonText: 'Cancel',
    shouldCloseAfterSubmit: true,
    danger: true,
  }),
  menuItem: () => ({
    closeMenu: (event) => { handleSubmit(event); }
  }),
  deleteIcon: () => ({
    style: {
      margin: '5px',
    },
    icon: iconDelete,
    name: iconDelete,
    role: 'img',
    fill: 'grey',
    fillRule: '',
    width: '',
    height: '',
    description: 'This is a description of the icon and what it does in context',
    iconTitle: '',
    className: 'extra-class',
  }),
};

// On submit we should be passed the values, not have to look them up
const handleSubmit = (services_credential_name, services_credential_host, services_credential_port, services_credential_proxy, services_credential_url_pattern, services_credential_user_attribute, services_credential_user_attribute_encoding, services_credential_enc_key, services_credential_ssl_server_dn, services_credential_ssl_sni, services_credential_authentication_sso_endpoint, services_credential_authentication_sso_client_id, services_credential_authentication_sso_client_secret, services_credential_authentication_sso_payload, services_credential_authentication_access_token_token, services_credential_authentication_ba_username, services_credential_authentication_ba_password,  loadGrid, setOpen) => {
 
  // Refresh data
  loadGrid(services_credential_name, services_credential_host, services_credential_port, services_credential_proxy, services_credential_url_pattern, services_credential_user_attribute, services_credential_user_attribute_encoding, services_credential_enc_key, services_credential_ssl_server_dn, services_credential_ssl_sni, services_credential_authentication_sso_endpoint, services_credential_authentication_sso_client_id, services_credential_authentication_sso_client_secret, services_credential_authentication_sso_payload, services_credential_authentication_access_token_token, services_credential_authentication_ba_username, services_credential_authentication_ba_password,  false);

  return true;
}

// The implementation of the Modal
class ServicescredentialDeleteModal extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      row: props.row,
      loadGrid: props.loadGrid,
      services_credential_name: this.props.row.cells[0].value,
      services_credential_host: this.props.row.cells[1].value,
      services_credential_port: this.props.row.cells[2].value,
      services_credential_proxy: this.props.row.cells[3].value,
      services_credential_url_pattern: this.props.row.cells[4].value,
      services_credential_user_attribute: this.props.row.cells[5].value,
      services_credential_user_attribute_encoding: this.props.row.cells[6].value,
      services_credential_enc_key: this.props.row.cells[7].value,
      open: false,
    }
  }

  render() {
    // const { open } = this.state.open;
    const { size, ...rest } = deleteProps.composedModal();
    const { hasScrollingContent, ...bodyProps } = deleteProps.modalBody();

    return (
      <ModalStateManager
        renderLauncher={({ setOpen }) => (
          <Icon {...deleteProps.deleteIcon()} onClick={() => setOpen(true)} title={this.state.id} />
        )}>
        {({ open, setOpen }) => (
          <ComposedModal
            {...rest}
            open={open}
            row={this.props.row}
            loadGrid={this.state.loadGrid}
            size={size || undefined}
            onClose={() => setOpen(false)}>
            <ModalHeader {...deleteProps.modalHeader({ titleOnly: true, name: this.state.code + ' ' + this.state.model })} />
            <ModalBody />
            <ModalFooter {...deleteProps.modalFooter()} shouldCloseAfterSubmit={true} onRequestSubmit={() => { handleSubmit(this.state.services_credential_name,this.state.services_credential_host,this.state.services_credential_port,this.state.services_credential_proxy,this.state.services_credential_url_pattern,this.state.services_credential_user_attribute,this.state.services_credential_user_attribute_encoding,this.state.services_credential_enc_key,this.state.services_credential_ssl_server_dn,this.state.services_credential_ssl_sni,this.state.services_credential_authentication_sso_endpoint,this.state.services_credential_authentication_sso_client_id,this.state.services_credential_authentication_sso_client_secret,this.state.services_credential_authentication_sso_payload,this.state.services_credential_authentication_access_token_token,this.state.services_credential_authentication_ba_username,this.state.services_credential_authentication_ba_password, this.state.loadGrid, setOpen); }} />
          </ComposedModal>
        )}
      </ModalStateManager>
    );
  }

  
}

export default ServicescredentialDeleteModal;
